import { call, put, takeEvery, delay } from 'redux-saga/effects';
import {
  GET_FEEDBACK_LIST_ASYNC,
  GET_FEEDBACK_LIST_PROGRESS,
  GET_FEEDBACK_PENDING_LIST_SUCCESS,
  GET_FEEDBACK_COMPLETED_LIST_SUCCESS,
  GET_FEEDBACK_TYPE_PROGRESS,
  GET_FEEDBACK_TYPE_SUCCESS,
  GET_FEEDBACK_TYPE_FAILED,
  GET_FEEDBACK_TYPE_ASYNC,
  GET_FEEDBACK_DETAIL_PROGRESS,
  GET_FEEDBACK_DETAIL_SUCCESS,
  GET_FEEDBACK_DETAIL_FAILED,
  GET_FEEDBACK_DETAIL_ASYNC,
  SUBMIT_FEEDBACK_RESPONSE_ASYNC,
  SUBMIT_FEEDBACK_RESPONSE_PROGRESS,
  SUBMIT_FEEDBACK_RESPONSE_SUCCESS,
  SUBMIT_FEEDBACK_RESPONSE_FAILED,
  CREATE_FEEDBACK_ASYNC,
  CREATE_FEEDBACK_PROGRESS,
  CREATE_FEEDBACK_SUCCESS,
  CREATE_FEEDBACK_FAILED,
  GET_FEEDBACK_FILTER_SUCCESS,
} from './contants';
import { ResponseApi } from '../../store/types';
import { HIDE_LOADING_OVERLAY, SHOW_LOADING_OVERLAY } from '../app/constants';
import {
  GetFeedbackListPayload,
  FeedbackListResponse,
  FeedbackItem,
  FeedbackTypeItem,
  SubmitFeedbackResponsePayload,
  FileToSend,
  CreateFeedbackPayload,
} from './types.ts';
import {
  fetchGetFeedBackDetail,
  fetchGetFeedbackList,
  fetchGetFeedbackType,
  fetchSubmitFeedbackResponse,
  fetchCreateFeedback,
} from '../../../api/feedback.ts';
import { fetchPresignedUrlS3 } from '../../../api/app';
import {
  compressImage,
  compressVideo,
} from '../../../../../Content/src/helper/ImageVideo.helper';
import axios from 'axios';
import Toast from 'react-native-toast-message';
import i18n from '../../../i18n/index.ts';
import { logger } from '../../../utils/index.tsx';
import {
  getDefaultFileInfo,
  isVideoFile,
} from '../../../../../Content/src/helper/Feedback.ts';
import { SubmissionSourceType } from './types.ts';

const getFeedbackList = function* (action: {
  type: string;
  payload: GetFeedbackListPayload;
}): Generator<any, void, any> {
  try {
    yield put({ type: GET_FEEDBACK_LIST_PROGRESS, payload: true });
    yield put({ type: HIDE_LOADING_OVERLAY });

    const response = (yield call(
      fetchGetFeedbackList,
      action.payload,
    )) as ResponseApi;

    if (response.status !== 200) {
      yield call(Toast.show, {
        text1: i18n.t('GET_FEEDBACK_LIST_FAILED'),
        type: 'error',
      });
      return;
    }

    const feedbackData = response.data as FeedbackListResponse;
    const items = feedbackData?.items || [];

    const pendingList: FeedbackItem[] = items.filter(item => item.status !== 2);
    const completedList: FeedbackItem[] = items.filter(
      item => item.status === 2,
    );

    yield put({
      type: GET_FEEDBACK_FILTER_SUCCESS,
      payload: action.payload,
    });

    yield put({
      type: GET_FEEDBACK_PENDING_LIST_SUCCESS,
      payload: pendingList,
    });

    yield put({
      type: GET_FEEDBACK_COMPLETED_LIST_SUCCESS,
      payload: completedList,
    });
  } catch (error) {
  } finally {
    yield put({ type: GET_FEEDBACK_LIST_PROGRESS, payload: false });
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
};

const getFeedbackType = function* (): Generator<any, void, any> {
  try {
    yield put({ type: GET_FEEDBACK_TYPE_PROGRESS, payload: true });
    yield put({ type: HIDE_LOADING_OVERLAY });
    const response = (yield call(fetchGetFeedbackType)) as ResponseApi;
    if (response.status !== 200) {
      yield put({ type: GET_FEEDBACK_TYPE_FAILED, payload: response.message });
      return;
    }
    const feedbackTypeData = response.data.map((item: FeedbackTypeItem) => ({
      value: item.id,
      label: item.feedbackType,
    }));

    yield put({
      type: GET_FEEDBACK_TYPE_SUCCESS,
      payload: feedbackTypeData,
      feedbackTypeFullList: response.data,
    });
  } catch (error) {
  } finally {
    yield put({ type: GET_FEEDBACK_TYPE_PROGRESS, payload: false });
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
};

const getFeedbackDetail = function* (action: {
  type: string;
  payload: string;
}): Generator<any, void, any> {
  try {
    yield put({ type: GET_FEEDBACK_DETAIL_PROGRESS, payload: true });
    yield put({ type: HIDE_LOADING_OVERLAY });

    const response = (yield call(
      fetchGetFeedBackDetail,
      action.payload,
    )) as ResponseApi;

    if (response.status !== 200) {
      yield put({
        type: GET_FEEDBACK_DETAIL_FAILED,
        payload: response.message,
      });
      return;
    }
    yield put({ type: GET_FEEDBACK_DETAIL_SUCCESS, payload: response.data });
  } catch (error) {
  } finally {
    yield put({ type: GET_FEEDBACK_DETAIL_PROGRESS, payload: false });
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
};

const submitFeedbackResponse = function* (action: {
  type: string;
  payload: SubmitFeedbackResponsePayload & {
    onSuccess?: () => void;
    onError?: (error: any) => void;
  };
}): Generator<any, void, any> {
  const { onSuccess, onError, images, ...submitPayload } = action.payload;

  try {
    yield put({ type: SHOW_LOADING_OVERLAY });
    yield put({ type: SUBMIT_FEEDBACK_RESPONSE_PROGRESS, payload: true });

    let filesToSend: FileToSend[] = [];

    // Process and upload images/videos to S3 if present
    if (images && images.length > 0) {
      for (const file of images) {
        try {
          // Determine file info based on mime type
          const defaultInfo = getDefaultFileInfo(file.mime);
          const fileName = file.name || defaultInfo.name;
          const isVideo = isVideoFile(file.mime);

          // Step 1: Compress image or video
          let compressedFile: { uri: string; size: number | undefined } | null =
            null;

          if (isVideo) {
            compressedFile = yield call(compressVideo, file.path || file.uri);
          } else {
            compressedFile = yield call(compressImage, file.path || file.uri);
          }

          if (!compressedFile) {
            logger('Failed to compress file:', fileName);
            continue;
          }

          // Step 2: Get presigned URL from S3
          const uploadFileData = {
            folder: 'feedback',
            fileName: fileName,
            contentType: file.mime,
          };

          const presignedResponse: any = yield call(
            fetchPresignedUrlS3,
            uploadFileData,
          );

          // Check response structure: presignedResponse.data is the actual data
          if (!presignedResponse?.data) {
            logger('Failed to get presigned URL for:', fileName);
            continue;
          }

          const {
            url: presignedUrl,
            key,
            fileId,
            s3BaseUrl,
          } = presignedResponse.data;

          if (!presignedUrl || typeof presignedUrl !== 'string') {
            logger('Invalid presigned URL for:', fileName);
            continue;
          }

          // Step 3: Upload compressed file to S3 using presigned URL
          // This is required because presigned URL only gives permission to upload
          // The file doesn't exist on S3 until we actually upload it
          try {
            const fetchResponse: Response = yield call(
              fetch,
              compressedFile.uri,
            );
            const fileBlob: Blob = yield call([fetchResponse, 'blob']);

            yield call(axios.put, presignedUrl, fileBlob, {
              headers: {
                'Content-Type': file.mime,
              },
              transformRequest: (data: any, headers: any) => {
                delete headers.Accept;
                delete headers['Content-Length'];
                return data;
              },
            });
          } catch (putError) {
            logger('Failed to upload to S3:', putError);
            continue;
          }

          // Step 4: Add to filesToSend array with correct format
          // Now the file exists on S3 and URL is valid
          const fileExtension = '.' + fileName.split('.').pop();

          // Construct the final S3 URL (without query params)
          const uploadFileUrl = s3BaseUrl
            ? `${s3BaseUrl}/${key}`
            : presignedUrl.split('?')[0];

          filesToSend.push({
            fileId: fileId,
            fileName: fileName,
            fileLength: compressedFile.size || 0,
            fileExtension: fileExtension,
            key: key,
            url: uploadFileUrl,
          });

          yield delay(300);
        } catch (uploadError) {
          logger('Error processing/uploading file:', uploadError);
        }
      }
    }

    // Step 5: Submit feedback response with proper payload structure
    const payload = {
      id: submitPayload.feedbackId,
      feedbackResponse: {
        comment: submitPayload.message.trim(),
        feedBackAttachments: filesToSend,
      },
    };

    const response: any = yield call(fetchSubmitFeedbackResponse, payload);

    if (response.status !== 200 || response.succeeded === false) {
      yield put({
        type: SUBMIT_FEEDBACK_RESPONSE_FAILED,
        payload: response.message,
      });

      yield call(Toast.show, {
        text1: response.message || i18n.t('SUBMIT_FEEDBACK_RESPONSE_FAILED'),
        type: 'error',
      });

      if (onError) {
        onError(response.message);
      }
      return;
    }

    yield put({ type: SUBMIT_FEEDBACK_RESPONSE_SUCCESS });
    yield call(Toast.show, {
      text1: response.message || i18n.t('SUBMIT_FEEDBACK_RESPONSE_SUCCESS'),
      type: 'success',
    });

    // Refresh feedback detail after successful submission
    yield put({
      type: GET_FEEDBACK_DETAIL_ASYNC,
      payload: submitPayload.feedbackId,
    });

    if (onSuccess) {
      onSuccess();
    }
  } catch (error: any) {
    logger('Error in submitFeedbackResponse:', error);
    yield put({
      type: SUBMIT_FEEDBACK_RESPONSE_FAILED,
      payload: error?.message,
    });

    yield call(Toast.show, {
      text1: i18n.t('SUBMIT_FEEDBACK_RESPONSE_FAILED'),
      type: 'error',
    });

    if (onError) {
      onError(error);
    }
  } finally {
    yield put({ type: SUBMIT_FEEDBACK_RESPONSE_PROGRESS, payload: false });
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
};

const createFeedback = function* (action: {
  type: string;
  payload: CreateFeedbackPayload;
}): Generator<any, void, any> {
  const { onSuccess, onError, ...submitPayload } = action.payload;

  try {
    yield put({ type: SHOW_LOADING_OVERLAY });
    yield put({ type: CREATE_FEEDBACK_PROGRESS, payload: true });

    const formData = new FormData();
    formData.append('FeedbackTypeId', submitPayload.feedbackTypeId);
    formData.append('Description', submitPayload.description);
    formData.append('PageIssue', submitPayload.pageIssue);

    if (submitPayload.attachments && submitPayload.attachments.length > 0) {
      submitPayload.attachments.forEach((file: any, index: number) => {
        if (file.uri && file.mime) {
          // FormData requires specific format for React Native
          const fileObj: any = {
            uri: file.uri,
            type: file.mime, // Must be mime type like 'image/jpeg', not 'image'
            name:
              file.fileName ||
              `file${index}.${file.mime.split('/')[1] || 'jpg'}`,
          };
          formData.append('Attachments', fileObj);
        }
      });
    }

    if (submitPayload.accountIds && submitPayload.accountIds.length > 0) {
      submitPayload.accountIds.forEach((accountId: string) => {
        formData.append('AccountIds', accountId);
      });
    }

    formData.append(
      'SubmissionSourceType',
      SubmissionSourceType.ApplicationWorker,
    );

    const response: any = yield call(fetchCreateFeedback, formData);

    if (response.status !== 200 || response.succeeded === false) {
      yield put({
        type: CREATE_FEEDBACK_FAILED,
        payload: response.message,
      });

      yield call(Toast.show, {
        text1: response.message || i18n.t('CREATE_FEEDBACK_FAILED'),
        type: 'error',
      });

      if (onError) {
        onError(response.message);
      }
      return;
    }

    yield put({ type: CREATE_FEEDBACK_SUCCESS });
    yield call(Toast.show, {
      text1: response.message || i18n.t('CREATE_FEEDBACK_SUCCESS'),
      type: 'success',
    });

    if (onSuccess) {
      onSuccess();
    }
  } catch (error: any) {
    logger('Error in createFeedback:', error);
    yield put({
      type: CREATE_FEEDBACK_FAILED,
      payload: error?.message,
    });

    yield call(Toast.show, {
      text1: i18n.t('CREATE_FEEDBACK_FAILED'),
      type: 'error',
    });

    if (onError) {
      onError(error);
    }
  } finally {
    yield put({ type: CREATE_FEEDBACK_PROGRESS, payload: false });
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
};

export default function* feedbackSaga() {
  yield takeEvery(GET_FEEDBACK_LIST_ASYNC, getFeedbackList);
  yield takeEvery(GET_FEEDBACK_TYPE_ASYNC, getFeedbackType);
  yield takeEvery(GET_FEEDBACK_DETAIL_ASYNC, getFeedbackDetail);
  yield takeEvery(SUBMIT_FEEDBACK_RESPONSE_ASYNC, submitFeedbackResponse);
  yield takeEvery(CREATE_FEEDBACK_ASYNC, createFeedback);
}
