import {
  GET_FEEDBACK_LIST_ASYNC,
  GET_FEEDBACK_LIST_FAILED,
  GET_FEEDBACK_PENDING_LIST_SUCCESS,
  GET_FEEDBACK_COMPLETED_LIST_SUCCESS,
  CLEAR_FEEDBACK_LIST,
  GET_FEEDBACK_TYPE_ASYNC,
  GET_FEEDBACK_TYPE_SUCCESS,
  GET_FEEDBACK_TYPE_FAILED,
  GET_FEEDBACK_TYPE_PROGRESS,
  GET_FEEDBACK_DETAIL_ASYNC,
  GET_FEEDBACK_DETAIL_PROGRESS,
  GET_FEEDBACK_DETAIL_FAILED,
  GET_FEEDBACK_DETAIL_SUCCESS,
  SUBMIT_FEEDBACK_RESPONSE_ASYNC,
  SUBMIT_FEEDBACK_RESPONSE_SUCCESS,
  SUBMIT_FEEDBACK_RESPONSE_FAILED,
  SUBMIT_FEEDBACK_RESPONSE_PROGRESS,
  CREATE_FEEDBACK_ASYNC,
  CREATE_FEEDBACK_SUCCESS,
  CREATE_FEEDBACK_FAILED,
  CREATE_FEEDBACK_PROGRESS,
  GET_FEEDBACK_FILTER_RESET,
  GET_FEEDBACK_FILTER_FAILED,
  GET_FEEDBACK_FILTER_SUCCESS,
} from './contants';
import {
  GetFeedbackListPayload,
  FeedbackItem,
  FeedbackTypeItem,
  FeedbackResponse,
  SubmitFeedbackResponsePayload,
  CreateFeedbackPayload,
} from './types';

// action saga
export const getFeedbackListAsync = (payload: GetFeedbackListPayload) =>
  ({ type: GET_FEEDBACK_LIST_ASYNC, payload } as const);

export const getFeedbackTypeAsync = () =>
  ({ type: GET_FEEDBACK_TYPE_ASYNC } as const);

export const getFeedbackDetailAsync = (payload: string) =>
  ({ type: GET_FEEDBACK_DETAIL_ASYNC, payload } as const);

export const createFeedbackAsync = (payload: CreateFeedbackPayload) =>
  ({ type: CREATE_FEEDBACK_ASYNC, payload } as const);
// action reducer

export const getFeedbackPendingListSuccess = (payload: FeedbackItem[]) =>
  ({ type: GET_FEEDBACK_PENDING_LIST_SUCCESS, payload } as const);
export const getFeedbackCompletedListSuccess = (payload: FeedbackItem[]) =>
  ({ type: GET_FEEDBACK_COMPLETED_LIST_SUCCESS, payload } as const);
export const getFeedbackListFailed = (payload: any) =>
  ({ type: GET_FEEDBACK_LIST_FAILED, payload } as const);
export const clearFeedbackList = () => ({ type: CLEAR_FEEDBACK_LIST } as const);

export const getFeedbackTypeSuccess = (
  payload: FeedbackTypeItem[],
  feedbackTypeFullList?: any[],
) =>
  ({
    type: GET_FEEDBACK_TYPE_SUCCESS,
    payload,
    feedbackTypeFullList,
  } as const);
export const getFeedbackTypeFailed = (payload: any) =>
  ({ type: GET_FEEDBACK_TYPE_FAILED, payload } as const);
export const getFeedbackTypeProgress = (payload: boolean) =>
  ({ type: GET_FEEDBACK_TYPE_PROGRESS, payload } as const);

export const getFeedbackDetailSuccess = (payload: FeedbackResponse) =>
  ({ type: GET_FEEDBACK_DETAIL_SUCCESS, payload } as const);
export const getFeedbackDetailFailed = (payload: any) =>
  ({ type: GET_FEEDBACK_DETAIL_FAILED, payload } as const);
export const getFeedbackDetailProgress = (payload: boolean) =>
  ({ type: GET_FEEDBACK_DETAIL_PROGRESS, payload } as const);

export const submitFeedbackResponseAsync = (
  payload: SubmitFeedbackResponsePayload & {
    onSuccess?: () => void;
    onError?: (error: any) => void;
  },
) => ({ type: SUBMIT_FEEDBACK_RESPONSE_ASYNC, payload } as const);
export const submitFeedbackResponseSuccess = () =>
  ({ type: SUBMIT_FEEDBACK_RESPONSE_SUCCESS } as const);
export const submitFeedbackResponseFailed = (payload: any) =>
  ({ type: SUBMIT_FEEDBACK_RESPONSE_FAILED, payload } as const);
export const submitFeedbackResponseProgress = (payload: boolean) =>
  ({ type: SUBMIT_FEEDBACK_RESPONSE_PROGRESS, payload } as const);

export const createFeedbackSuccess = () =>
  ({ type: CREATE_FEEDBACK_SUCCESS } as const);
export const createFeedbackFailed = (payload: any) =>
  ({ type: CREATE_FEEDBACK_FAILED, payload } as const);
export const createFeedbackProgress = (payload: boolean) =>
  ({ type: CREATE_FEEDBACK_PROGRESS, payload } as const);

export const getFeedbackFilterSuccess = (payload: GetFeedbackListPayload) =>
  ({ type: GET_FEEDBACK_FILTER_SUCCESS, payload } as const);
export const getFeedbackFilterFailed = (payload: any) =>
  ({ type: GET_FEEDBACK_FILTER_FAILED, payload } as const);
export const getFeedbackFilterReset = () =>
  ({ type: GET_FEEDBACK_FILTER_RESET } as const);

export const actionFeedback = {
  getFeedbackListAsync,
  getFeedbackPendingListSuccess,
  getFeedbackCompletedListSuccess,
  getFeedbackListFailed,
  clearFeedbackList,
  getFeedbackTypeAsync,
  getFeedbackTypeSuccess,
  getFeedbackTypeFailed,
  getFeedbackTypeProgress,
  getFeedbackDetailAsync,
  getFeedbackDetailSuccess,
  getFeedbackDetailFailed,
  getFeedbackDetailProgress,
  submitFeedbackResponseAsync,
  submitFeedbackResponseSuccess,
  submitFeedbackResponseFailed,
  submitFeedbackResponseProgress,
  createFeedbackAsync,
  createFeedbackSuccess,
  createFeedbackFailed,
  createFeedbackProgress,
  getFeedbackFilterSuccess,
  getFeedbackFilterFailed,
  getFeedbackFilterReset,
};

export type FeedbackAction = ReturnType<
  | typeof getFeedbackListAsync
  | typeof getFeedbackPendingListSuccess
  | typeof getFeedbackCompletedListSuccess
  | typeof getFeedbackListFailed
  | typeof clearFeedbackList
  | typeof getFeedbackTypeAsync
  | typeof getFeedbackTypeSuccess
  | typeof getFeedbackTypeFailed
  | typeof getFeedbackTypeProgress
  | typeof getFeedbackDetailAsync
  | typeof getFeedbackDetailSuccess
  | typeof getFeedbackDetailFailed
  | typeof getFeedbackDetailProgress
  | typeof submitFeedbackResponseAsync
  | typeof submitFeedbackResponseSuccess
  | typeof submitFeedbackResponseFailed
  | typeof submitFeedbackResponseProgress
  | typeof createFeedbackAsync
  | typeof createFeedbackSuccess
  | typeof createFeedbackFailed
  | typeof createFeedbackProgress
  | typeof getFeedbackFilterSuccess
  | typeof getFeedbackFilterFailed
  | typeof getFeedbackFilterReset
>;
