import client from './configAxios';
import { logger } from '../utils';
import {
  presignedUrl as fetchPresignedUrlEndpoint,
  getPresignedUrlS3,
  getVersionApp,
} from './endpoint';
import { AxiosProgressEvent } from 'axios';

export const fetchPresignedUrl = async (key: string) => {
  try {
    const response = await client.get(`${fetchPresignedUrlEndpoint}/${key}`);
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchPresignedUrl', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

export const fetchPresignedUrlS3 = async (uploadFileData: {
  folder: string;
  fileName: string;
  contentType: string;
  onUploadProgress?: (progress: AxiosProgressEvent) => void;
}) => {
  try {
    const response = await client.post(getPresignedUrlS3, uploadFileData, {
      onUploadProgress: progressEvent => {
        uploadFileData?.onUploadProgress?.(progressEvent);
      },
    });
    return {
      code: response?.data?.statusCode,
      data: response?.data?.data,
      message: response?.data?.message,
    };
  } catch (error: any) {
    return {
      code: error?.response?.status ?? 500,
      message:
        error?.response?.data?.message ??
        error?.message ??
        'fetchPresignedUrlS3',
    };
  }
};

export const fetchGetVersionApp = async () => {
  try {
    const response = await client.get(getVersionApp);
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchGetVersionApp', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};
