// Helper function to detect if file is video
export const isVideoFile = (mime: string): boolean => {
  return mime.startsWith('video/');
}

// Helper function to get default file name and extension based on mime type
export const getDefaultFileInfo = (mime: string): { name: string; extension: string } => {
  if (isVideoFile(mime)) {
    const extension = mime.split('/')[1] || 'mp4';
    return {
      name: `feedback-video-${Date.now()}.${extension}`,
      extension: extension,
    };
  } else {
    const extension = mime.split('/')[1] || 'jpg';
    return {
      name: `feedback-image-${Date.now()}.${extension}`,
      extension: extension,
    };
  }
}
