import { client } from './configAxios';
import { logger } from '../utils';
import {
  getFeedBackDetail,
  getFeedbackList,
  getFeedbackType,
  submitFeedBack,
  createFeedback,
} from './endpoint';
import { GetFeedbackListPayload } from '../redux/features/feedback/types';

export const fetchGetFeedbackList = async (
  payload: GetFeedbackListPayload | undefined,
) => {
  try {
    const { feedbackStatusParam, feedbackTypeIdParam } = payload || {};
    let url = getFeedbackList;
    const params = [];

    if (feedbackStatusParam) {
      params.push(`Status=${feedbackStatusParam}`);
    }

    if (feedbackTypeIdParam) {
      params.push(`FeedbackTypeId=${feedbackTypeIdParam}`);
    }

    if (params.length > 0) {
      url = `${getFeedbackList}?${params.join('&')}`;
    }

    const response = await client.get(url);

    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchGetFeedbackList', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

export const fetchGetFeedbackType = async () => {
  try {
    const response = await client.get(getFeedbackType);
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchGetFeedbackType', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
    };
  }
};

export const fetchGetFeedBackDetail = async (feedbackId: string) => {
  try {
    const response = await client.get(`${getFeedBackDetail}/${feedbackId}`);
    return {
      status: response.status,
      code: response.status,
      data: response.data?.data,
    };
  } catch (error: any) {
    logger('error fetchGetFeedBackDetail', error);
  }
};

export const fetchSubmitFeedbackResponse = async (payload: any) => {
  try {
    const response = await client.post(submitFeedBack, payload);
    return {
      status: response.status,
      code: response.status,
      succeeded: response.data?.succeeded,
      data: response.data?.data,
      message: response.data?.message,
    };
  } catch (error: any) {
    logger('error fetchSubmitFeedbackResponse', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
      succeeded: false,
    };
  }
};

export const fetchCreateFeedback = async (payload: any) => {
  try {
    const response = await client.post(createFeedback, payload, {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    });
    return {
      status: response.status,
      code: response.status,
      succeeded: response.data?.succeeded,
      data: response.data?.data,
      message: response.data?.message,
    };
  } catch (error: any) {
    logger('error fetchCreateFeedback', error);
    return {
      message: error?.response?.data?.message,
      code: error?.code,
      status: error?.status,
      succeeded: false,
    };
  }
};
