import { FeedbackAction } from './actions';
import {
  CLEAR_FEEDBACK_LIST,
  GET_FEEDBACK_COMPLETED_LIST_SUCCESS,
  GET_FEEDBACK_DETAIL_SUCCESS,
  GET_FEEDBACK_FILTER_SUCCESS,
  GET_FEEDBACK_PENDING_LIST_SUCCESS,
  GET_FEEDBACK_TYPE_SUCCESS,
} from './contants';
import { FeedbackData } from './types';

export const initialState: FeedbackData = {
  pendingList: [],
  completedList: [],
  feedbackType: [],
  feedbackTypeFullList: [],
  feedbackDetail: null,
  feedbackFilter: {},
};

export default function feedbackReducer(
  state = initialState,
  action: FeedbackAction,
): FeedbackData {
  switch (action.type) {
    case GET_FEEDBACK_PENDING_LIST_SUCCESS:
      return {
        ...state,
        pendingList: action.payload,
      };
    case GET_FEEDBACK_COMPLETED_LIST_SUCCESS:
      return {
        ...state,
        completedList: action.payload,
      };
    case GET_FEEDBACK_TYPE_SUCCESS:
      return {
        ...state,
        feedbackType: action.payload,
        feedbackTypeFullList: action.feedbackTypeFullList || [],
      };
    case GET_FEEDBACK_DETAIL_SUCCESS:
      return {
        ...state,
        feedbackDetail: action.payload,
      };

    case GET_FEEDBACK_FILTER_SUCCESS:
      return {
        ...state,
        feedbackFilter: action.payload,
      };

    case CLEAR_FEEDBACK_LIST:
      return {
        ...initialState,
      };
    default:
      return state;
  }
}
