import { call, put, takeEvery, delay } from 'redux-saga/effects';
import {
  FETCH_GET_VERSION_APP_ASYNC,
  FETCH_GET_VERSION_APP_FAILED,
  FETCH_GET_VERSION_APP_SUCCESS,
  HIDE_ALERT,
  HIDE_ALERT_ASYNC,
  HIDE_LOADING_OVERLAY,
  HIDE_LOADING_OVERLAY_ASYNC,
  SHOW_ALERT,
  SHOW_ALERT_ASYNC,
  SHOW_LOADING_OVERLAY,
  SHOW_LOADING_OVERLAY_ASYNC,
  UPLOAD_FILE_S3_ASYNC,
  UPLOAD_FILE_S3_FAILED,
  UPLOAD_FILE_S3_PROGRESS,
  UPLOAD_FILE_S3_SUCCESS,
  SET_UPLOADED_FILES,
} from './constants';
import {
  AppAlertModalProps,
  AppLoadingOverlayProps,
  UploadFileS3Request,
  S3UploadedFile,
} from './types';
import { fetchPresignedUrlS3 } from '../../../api/app';
import axios from 'axios';
import { fetchGetVersionApp } from '../../../api/app';
import { ResponseApi } from '../../store/types';

function* handleShowAlert(action: {
  type: string;
  payload: AppAlertModalProps;
}) {
  yield put({ type: SHOW_ALERT, payload: action.payload });
}

function* handleHideAlert() {
  yield put({ type: HIDE_ALERT });
}

function* handleShowLoadingOverlay(action: {
  type: string;
  payload: AppLoadingOverlayProps;
}) {
  yield put({ type: SHOW_LOADING_OVERLAY, payload: action.payload });
}

function* handleHideLoadingOverlay() {
  yield put({ type: HIDE_LOADING_OVERLAY });
}


// Helper function to generate random numbers for progress simulation
function getRandomNumbers(count: number): number[] {
  const numbers: number[] = [];
  let current = 0;
  for (let i = 0; i < count - 1; i++) {
    const increment = Math.floor(
      Math.random() * ((100 - current) / (count - i)),
    );
    current += increment;
    numbers.push(current);
  }
  numbers.push(100);
  return numbers;
}

function* handleUploadFileS3(action: {
  type: string;
  payload: UploadFileS3Request;
}): Generator<any, void, any> {
  const { files, onSuccess, onError } = action.payload;

  try {
    yield put({ type: UPLOAD_FILE_S3_PROGRESS, payload: 0 });
    const uploadedResults: S3UploadedFile[] = [];

    for (const fileData of files) {
      const randomNumbers = getRandomNumbers(5);

      try {
        const { file, folderName } = fileData;
        const fileObj = file.originFileObj || {};

        if (!fileObj.uri) {
          uploadedResults.push({
            success: false,
            loading: false,
          });
          continue;
        }

        // Call getPresignedUrl API
        const uploadFileData = {
          folder: folderName,
          fileName: file.name,
          contentType: file.type || 'application/octet-stream',
        };

        yield put({ type: UPLOAD_FILE_S3_PROGRESS, payload: randomNumbers[0] });

        const response: any = yield call(fetchPresignedUrlS3, uploadFileData);

        const resData = response?.data;

        if (!response?.succeeded || !resData) {
          uploadedResults.push({
            success: false,
            loading: false,
          });
          continue;
        }

        const {
          url: presignedUrl,
          key: objectKey,
          s3BaseUrl,
          fileId,
          fileUrl,
        } = resData;

        if (!presignedUrl || typeof presignedUrl !== 'string') {
          uploadedResults.push({
            success: false,
            loading: false,
          });
          continue;
        }

        yield put({ type: UPLOAD_FILE_S3_PROGRESS, payload: randomNumbers[1] });

        // Fetch file blob and upload to S3
        try {
          const fetchResponse: Response = yield call(fetch, fileObj.uri);
          const fileBlob: Blob = yield call([fetchResponse, 'blob']);

          yield call(axios.put, presignedUrl, fileBlob, {
            headers: {
              'Content-Type': file.type || 'application/octet-stream',
            },
            transformRequest: (data: any, headers: any) => {
              delete headers.Accept;
              delete headers['Content-Length'];
              return data;
            },
          });

          yield put({
            type: UPLOAD_FILE_S3_PROGRESS,
            payload: randomNumbers[2],
          });

          // Create S3 URL
          const uploadFileUrl = s3BaseUrl
            ? `${s3BaseUrl}/${objectKey}`
            : fileUrl;

          yield put({
            type: UPLOAD_FILE_S3_PROGRESS,
            payload: randomNumbers[4],
          });
          yield delay(500);
          yield put({ type: UPLOAD_FILE_S3_PROGRESS, payload: 100 });
          yield delay(300);

          uploadedResults.push({
            success: true,
            loading: false,
            uploadFileUrl: uploadFileUrl,
            objectKey: objectKey,
            fileId: fileId,
            fileName: file.name,
            fileType: file.type || 'application/octet-stream',
            fileSize: file.size,
            originUrl: fileUrl,
          });
        } catch (putError) {
          console.error('Error uploading file to S3:', putError);
          uploadedResults.push({
            success: false,
            loading: false,
          });
        }
      } catch (fileError) {
        console.error('Error processing file:', fileError);
        uploadedResults.push({
          success: false,
          loading: false,
        });
      }
    }

    // Save all uploaded results to state
    yield put({ type: SET_UPLOADED_FILES, payload: uploadedResults });
    yield put({ type: UPLOAD_FILE_S3_SUCCESS });
    yield put({ type: UPLOAD_FILE_S3_PROGRESS, payload: 0 });

    // Call success callback if provided
    if (onSuccess) {
      onSuccess(uploadedResults);
    }
  } catch (error) {
    console.error('Error uploading files to S3:', error);
    yield put({ type: UPLOAD_FILE_S3_FAILED });
    yield put({ type: UPLOAD_FILE_S3_PROGRESS, payload: 0 });

    // Call error callback if provided
    if (onError) {
      onError(error);
    }
  }
}

function* handleFetchGetVersionApp() {
  try {
    yield put({ type: SHOW_LOADING_OVERLAY });
    const response = (yield call(fetchGetVersionApp)) as ResponseApi;
    if (response.status !== 200) {
      yield put({
        type: FETCH_GET_VERSION_APP_FAILED,
        payload: response.message || 'Failed to get version app',
      });
      return;
    }
    yield put({ type: FETCH_GET_VERSION_APP_SUCCESS, payload: response.data });
  } catch (error) {
    yield put({ type: FETCH_GET_VERSION_APP_FAILED, payload: error });
  } finally {
    yield put({ type: HIDE_LOADING_OVERLAY });
  }
}

export default function* appSaga() {
  yield takeEvery(HIDE_ALERT_ASYNC, handleHideAlert);
  yield takeEvery(SHOW_ALERT_ASYNC, handleShowAlert);
  yield takeEvery(SHOW_LOADING_OVERLAY_ASYNC, handleShowLoadingOverlay);
  yield takeEvery(HIDE_LOADING_OVERLAY_ASYNC, handleHideLoadingOverlay);
  yield takeEvery(UPLOAD_FILE_S3_ASYNC, handleUploadFileS3);
  yield takeEvery(FETCH_GET_VERSION_APP_ASYNC, handleFetchGetVersionApp);
}
