export interface FeedbackItem {
  feedbackIndex: number;
  pageIssue: string;
  description: string;
  actionType: number | null;
  status: number;
  feedbackTypeId: string;
  feedbackType: string;
  targetDate: string | null;
  createdById: string;
  createdByName: string;
  createdByEmail: string;
  createdByUserTypes: number[];
  createdAt: string;
  completedById: string | null;
  completedByEmail: string | null;
  completedByName: string | null;
  completedByUserTypes: number[] | null;
  completedDate: string | null;
  feedbackAssignment: FeedbackAssignment[];
  owners: any;
  attachments: FeedbackAttachment[];
  id: string;
}

export interface FeedbackResponse {
  feedbackResponses: any;
  feedbackIndex: number;
  pageIssue: string;
  description: string;
  actionType: number | null;
  status: number;
  feedbackTypeId: string;
  feedbackType: string;
  targetDate: string | null;
  createdById: string;
  createdByName: string;
  createdByEmail: string;
  createdByUserTypes: number[];
  createdAt: string;
  completedById: string | null;
  completedByEmail: string | null;
  completedByName: string | null;
  completedByUserTypes: number[] | null;
  completedDate: string | null;
  feedbackAssignment: FeedbackAssignment[];
  owners: any;
  attachments: FeedbackAttachment[];
  translations: {
    [key: string]: {
      feedbackType: string;
    };
  };
  id: string;
}

export interface FeedbackData {
  pendingList: FeedbackItem[];
  completedList: FeedbackItem[];
  feedbackType: FeedbackTypeItem[];
  feedbackTypeFullList: FeedbackTypeItem[];
  feedbackDetail: FeedbackResponse | null;
  feedbackFilter: GetFeedbackListPayload;
}

export interface FeedbackListResponse {
  items: FeedbackItem[];
  pageIndex: number;
  pageSize: number;
  totalCount: number;
  totalPage: number;
}

export interface FeedbackResponse {
  data: FeedbackData;
  statusCode: number;
  succeeded: boolean;
  code: string;
  message: string;
  detailedError: any;
}

export interface GetFeedbackListPayload {
  feedbackStatusParam?: string;
  feedbackTypeIdParam?: string;
  pageIndex?: number;
  pageSize?: number;
  actionType?: number;
  status?: number;
  sortBy?: string;
  keyword?: string;
  filterBy?: string;
}

export interface FeedbackAttachment {
  attachmentId?: string;
  fileId?: string;
  fileName?: string;
  fileLength?: number;
  fileExtension?: string;
  key?: string;
  url?: string;
}

export interface FileToSend {
  fileId: string;
  fileName: string;
  fileLength: number;
  fileExtension: string;
  key: string;
  url: string;

  // type ly t th vin khi ly nh
  uri?: string;
  path?: string;
  mime?: string;
  name?: string;
  size?: number;
  type?: 'image' | 'video';
}

export interface FeedbackAssignment {
  accountId: string;
  accountName: string;
  accountEmail: string;
}

export interface FeedbackTypeOwner {
  id: string;
  name: string;
}

export interface FeedbackTypeTranslations {
  en: {
    feedbackType: string;
  };
  es: {
    feedbackType: string;
  };
}

export interface FeedbackTypeItem {
  id: string;
  feedbackType: string;
  owners: FeedbackTypeOwner[];
  translations: FeedbackTypeTranslations;
}

export interface SubmitFeedbackResponsePayload {
  feedbackId: string;
  message: string;
  images?: {
    uri: string;
    path: string;
    mime: string;
    name?: string;
    size?: number;
    type?: 'image' | 'video';
  }[];
}

export interface FeedbackAttachmentItem {
  uri: string;
  path?: string;
  mime: string;
  type: 'image' | 'video';
  fileName?: string;
}

export interface CreateFeedbackPayload {
  feedbackTypeId: string;
  pageIssue: string;
  description: string;
  attachments?: FeedbackAttachmentItem[];
  accountIds?: string[];
  submissionSourceType?: SubmissionSourceType;
  onSuccess?: () => void;
  onError?: (error: any) => void;
}

export enum SubmissionSourceType {
  ApplicationWorker = 4,
  ApplicationSupplier = 5,
}
