import {
  GET_NETWORK_FAILED,
  GET_NETWORK_PROGRESS,
  GET_NETWORK_SUCCESS,
  GET_REVIEWS_SUCCESS,
  UPDATE_DATA_NETWORK,
} from './contants';
import { NetworkAction } from './actions';
import { NetWork } from './types';

export const initialState: NetWork = {
  network: {
    data: null,
    isLoading: false,
    error: null,
  },
  reviews: [],
};

export default function homeReducer(
  state = initialState,
  action: NetworkAction,
): NetWork {
  switch (action.type) {
    case GET_NETWORK_SUCCESS:
      return {
        ...state,
        network: {
          ...state.network,
          data: action.payload,
          isLoading: false,
          error: null,
        },
      };
    case GET_NETWORK_FAILED:
      return {
        ...state,
        network: {
          ...state.network,
          isLoading: false,
          error: action.payload,
        },
      };
    case GET_NETWORK_PROGRESS:
      return {
        ...state,
        network: {
          ...state.network,
          isLoading: action.payload,
        },
      };
    case GET_REVIEWS_SUCCESS:
      return {
        ...state,
        reviews: action.payload,
      };
    case UPDATE_DATA_NETWORK:
      return {
        ...state,
        network: {
          ...state.network,
          data: {
            items: [
              ...(state.network.data?.items || []),
              ...action.payload.items,
            ],
            pageIndex: action.payload.pageIndex,
            pageSize: action.payload.pageSize,
            totalCount: action.payload.totalCount,
            totalPage: action.payload.totalPage,
          },
          isLoading: false,
          error: null,
        },
      };

    default:
      return state;
  }
}
